import Vue from 'vue';
import { debounce } from './index'


/**
 * v-debounce:keyup="[getList, 500]"
 * v-debounce:="[, ()]"
 */
Vue.directive('debounce', {
  bind (el, binding) {
    if (!binding.arg) {
      console.error('...');
      return;
    }
    if (binding.value instanceof Array && binding.value.length > 0) {
      let [fn, delay] = binding.value;
      el.addEventListener(binding.arg, () => {
        debounce(fn, delay)
      })
    } else {
      console.error('...');
    }
  }
})
